html, body {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
}
main {
  flex: 1 0 auto;
}
:root {
    --color-sub: #2d2d39;
  }
  
  .btn--sub {
    background-color: var(--color-sub-light);
  }
  
  .card {
    border: 2px solid var(--color-light);
  }
  
  img {
    width: 100%;
  }
  
  .tag > i.im {
    font-size: 1.5rem;
    margin-right: 0.5rem;
  }
  
  .content-box {
    width: 95%;
    max-width: 85rem;
    margin: 0 auto;
  }
  
  /*=======================
    Header Section
  ========================*/
  .header {
    position: sticky;
    z-index: 111;
    background-color: var(--color-sub);
    padding: 1.5rem 0;
  }
  
  .header > .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .header__logo > img {
    height: 3.8rem;
  }
  
  .header__nav > ul {
    display: inline-flex;
    align-items: center;
    list-style: none;
    gap: 6.5rem;
  }
  
  .header__menuItem > a {
    color: var(--color-light);
    font-weight: 300;
  }
  
  .toggle-menu > span {
    display: none;
  }
  
  .header__nav input[type='checkbox'] {
    display: none;
  }
  
  .header__nav input[type='checkbox']:checked ~ .header__menu {
    opacity: 1;
    pointer-events: all;
  }
  
  .project--thumbnail {
    height: 250px;
    object-fit: cover;
  }
  
  @media screen and (max-width: 800px) {
    .header__logo {
      position: relative;
      z-index: 999;
    }
    .toggle-menu {
      position: relative;
    }
  
    .toggle-menu > span {
      display: block;
      position: fixed;
      top: 1.5rem;
      right: 2.75rem;
      cursor: pointer;
      padding: 1rem;
      background: var(--color-sub-light);
      z-index: 999;
      font-size: 0;
      width: 5rem;
      height: 5rem;
      border-radius: 50%;
    }
  
    .toggle-menu__lines,
    .toggle-menu__lines::before,
    .toggle-menu__lines::after {
      pointer-events: none;
      content: '';
      display: block;
      height: 2px;
      width: 2.5rem;
      position: fixed;
      top: 4rem;
      right: 4rem;
      z-index: 999;
      background: var(--color-light);
      transition: all 0.3s ease-in-out;
    }
  
    .toggle-menu__lines::before {
      top: 3.4rem;
      transform-origin: 0 0;
    }
    .toggle-menu__lines::after {
      top: 4.6rem;
      transform-origin: 0 0;
    }
  
    .header__nav input[type='checkbox']:checked + .toggle-menu > .toggle-menu__lines::before {
      margin-left: 3px;
      transform: rotate(45deg) scaleX(1.2) translateX(-2px) translateY(-4px);
      visibility: visible;
    }
  
    .header__nav input[type='checkbox']:checked + .toggle-menu > .toggle-menu__lines {
      visibility: hidden;
    }
  
    .header__nav input[type='checkbox']:checked + .toggle-menu > .toggle-menu__lines::after {
      transform: rotate(-45deg) scaleX(1.2) translateX(-2px) translateY(3px);
      visibility: visible;
    }
  
    .header__nav > ul {
      opacity: 0;
      pointer-events: none;
      flex-direction: column;
      justify-content: center;
      position: fixed;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: var(--color-sub);
      transition: all 0.5s ease-in-out;
    }
  
    .header__menuItem a {
      transition: all 0.5s ease-in-out;
      padding: 1rem 3rem;
    }
  
    .header__menuItem a:hover {
      padding: 1rem 3rem;
      border-radius: 3px;
      background: var(--color-sub-light);
    }
  }
  
  /*=======================
    Hero Section
  ========================*/
  
  .hero-section {
    height: 50vh;
    display: flex;
    align-items: center;
    background-color: var(--color-sub);
    /* background-color: var(--font-base); */
    /* background: url('https://thumbs.dreamstime.com/b/food-background-breakfast-yogurt-granola-muesli-strawberries-banner-image-website-gray-concrete-table-top-view-214357125.jpg') no-repeat; */
    width: 100%;
    /* opacity: 0.9; */
  }
  
  .hero-section__box {
    margin-bottom: 4rem;
  }
  
  .hero-section__box > h2,
  .hero-section__box > h2 > span {
    font-size: 4rem;
    color: black;
    line-height: 1.3;
    text-transform: uppercase;
  }
  
  @media screen and (max-width: 1000px) {
    .hero-section__box > h2,
    .hero-section__box > h2 > span {
      font-size: 3.4rem;
    }
  }
  
  @media screen and (max-width: 800px) {
    .hero-section__box > h2,
    .hero-section__box > h2 > span {
      font-size: 3rem;
      line-height: 1.5;
    }
  }
  
  @media screen and (max-width: 500px) {
    .hero-section__box > h2,
    .hero-section__box > h2 > span {
      font-size: 2.4rem;
      line-height: 1.5;
    }
  }
  
  @media screen and (max-width: 400px) {
    .hero-section__box > h2,
    .hero-section__box > h2 > span {
      font-size: 2rem;
    }
  }
  
  .hero-section__box > h2 {
    font-weight: 300;
  }
  
  .hero-section__box > h2 > span {
    font-weight: 700;
  }
  
  .hero-section__search > .form {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 2rem;
  }
  
  .hero-section__search > .form label {
    position: absolute;
    left: -9999px;
  }
  
  .hero-section__search > .form input.input--text {
    width: 50rem;
  }
  
  @media screen and (max-width: 700px) {
    .hero-section__search > .form {
      display: flex;
      flex-direction: column;
      gap: 0;
      align-items: stretch;
    }
  
    .hero-section__search > .form input.input--text,
    .hero-section__search > .form input {
      min-width: 100%;
      width: 100%;
    }
  }
  
  /*=======================
    Developers
  ========================*/
  
  .devlist {
    margin-top: 4rem;
  }
  
  .devlist .grid {
    align-items: stretch;
  }
  
  .devlist .dev .dev__profile {
    display: flex;
    align-items: center;
    gap: 1.5rem;
  }
  
  .devlist .dev .dev__meta h3 {
    color: var(--color-sub);
    font-size: 2rem;
    font-weight: 700;
  }
  
  .devlist .dev .dev__meta h5 {
    color: var(--color-text);
    font-size: 1.4rem;
    font-weight: 500;
  }
  
  .devlist .dev .dev__info {
    margin: 1.5rem 0;
    font-size: 1.35rem;
    line-height: 1.4;
    text-align: justify;
  }
  
  /*=======================
    Pagination
  ========================*/
  .pagination {
    margin-top: 8rem;
    margin-bottom: 10rem;
  }
  
  .pagination > ul {
    flex-wrap: wrap;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
  }
  
  .pagination .btn {
    color: var(--color-sub);
  }
  
  .pagination .btn.btn--sub {
    color: var(--color-light);
  }
  
  /*=======================
    Profile
  ========================*/
  
  .profile .card > .dev {
    padding: 4rem;
  }
  
  .dev__avatar {
    display: block;
    margin: auto;
  }
  
  .dev__name {
    color: var(--color-sub);
    font-size: 2.8rem;
    font-weight: 700;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
  }
  
  .dev__title {
    font-size: 1.6rem;
    font-weight: 500;
    line-height: 1.3;
  }
  
  .dev__location {
    margin-top: 1rem;
  }
  
  .dev__social {
    margin: 3rem 0;
    list-style: none;
    display: flex;
    justify-content: center;
    gap: 1.5rem;
  }
  
  .devInfo {
    margin-bottom: 3rem;
  }
  
  .devInfo__title,
  .devInfo__subtitle {
    font-size: 2.6rem;
    color: var(--color-sub);
    text-transform: uppercase;
    font-weight: 700;
    margin-bottom: 1.5rem;
  }
  
  .devInfo__subtitle {
    font-size: 2rem;
    color: var(--color-sub-light);
  }
  
  .devInfo__about,
  .devSkill__info {
    font-size: 1.45rem;
    line-height: 1.7;
    text-align: justify;
  }
  
  .devSkill {
    margin-bottom: 3rem;
  }
  
  .devSkill__title {
    font-size: 1.65rem;
    font-weight: 500;
    color: var(--color-sub-light);
  }
  
  @media screen and (min-width: 700px) {
    .devSkill {
      display: flex;
      justify-content: space-between;
      gap: 3rem;
    }
  
    .devSkill__info {
      flex-basis: 80%;
    }
  }
  
  /*=======================
    Single Projects
  ========================*/
  
  .singleProject .column--1of3 {
    order: 2;
  }
  
  @media screen and (min-width: 1081px) {
    .singleProject .column--1of3 {
      order: 1;
    }
    .singleProject .column--2of3 {
      order: 2;
    }
  }
  
  .singleProject__toolStack {
    margin-top: 2rem;
    margin-bottom: 4rem;
  }
  
  .singleProject__liveLink {
    font-weight: 500;
    transition: all 0.3s ease-in-out;
    border-bottom: 2px solid transparent;
  }
  
  .singleProject__liveLink:hover {
    font-weight: 500;
    border-bottom: 2px solid var(--color-main);
  }
  
  .singleProject__liveLink i {
    display: inline-block;
    font-size: 1.6rem;
    margin-right: 1rem;
  }
  
  .singleProject__preview {
    /* height: 50vh; */
    object-fit: cover;
    object-position: top center;
    margin-bottom: 3rem;
    border-radius: 0.7rem;
  }
  
  .singleProject__developer {
    font-size: 1.8rem;
    font-weight: 500;
  }
  
  .singleProject__title {
    font-size: 3.6rem;
    font-weight: 700;
    color: var(--color-sub);
    margin-bottom: 3rem;
  }
  
  .project__author{
    font-size: 1.4rem;
    font-style: italic;
  }
  
  .project--rating{
    margin-bottom: 1.6em;
    margin-top: .5em;
  }
  
  .singleProject__subtitle {
    text-transform: uppercase;
    font-size: 2.4rem;
    font-weight: 700;
    color: var(--color-sub-light);
    margin-bottom: 1rem;
  }
  
  /*=======================
    Projects Page
  ========================*/
  .projects > .hero-section {
    background: var(--color-sub-lighter);
    height: 30vh;
  }
  
  .projects .hero-section__box {
    margin-bottom: 2.5rem;
  }
  
  .projects .hero-section__box > h2 {
    color: var(--color-sub);
  }
  
  .projects .hero-section__box > h2 > span {
    font-weight: 700;
    color: var(--color-sub);
  }
  
  .projectsList {
    margin-top: 8rem;
  }
  
  /*========== Projects ==========*/
  
  .project__thumbnail {
    height: 25rem;
    object-fit: cover;
    object-position: top center;
  }
  
  .project__title {
    font-size: 2rem;
    font-weight: 700;
    color: var(--color-sub);
  }
  
  .project__info {
    margin: 1rem 0;
    font-size: 1.4rem;
    text-align: justify;
  }
  
  /*========== Comments ==========*/
  
  .comments {
    margin-top: 4rem;
    padding-top: 3rem;
    border-top: 2px solid var(--color-light);
  }
  
  .comments .form label {
    position: absolute;
    margin: -9999px;
  }
  
  .commentList {
    margin: 3rem 0;
  }
  
  .comment {
    display: flex;
    gap: 2rem;
  }
  
  .comment:not(:last-child) {
    margin-bottom: 3rem;
  }
  
  .comment__author {
    font-size: 1.8rem;
  }
  
  .comment__info {
    font-size: 1.45rem;
  }
  
  /*=======================
    Account Settings
  ========================*/
  
  .settingsPage .dev > .settings__btn {
    display: inline-block;
    margin-bottom: 3rem;
  }
  
  .settings {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .settings__title {
    font-size: 2.4rem;
    text-transform: uppercase;
    font-weight: 700;
    color: var(--color-sub);
  }
  
  .settings__table {
    margin-top: 3rem;
    margin-bottom: 5rem;
    padding: 0 3rem;
    background: var(--color-white);
  }
  
  .settings__table tr {
    width: 100%;
  }
  
  .settings__table tr > td {
    padding: 2rem 0;
  }
  
  .settings__table tr:not(:last-child) td {
    border-bottom: 2px solid var(--color-white-light);
  }
  
  .settings__thumbnail {
    width: 23%;
    min-width: 10rem;
  }
  
  .settings__thumbnail img {
    padding-right: 3rem;
    height: 15rem;
    object-fit: contain;
  }
  
  .settings__tableInfo{
    width: 100%;
  }
  
  .settings__tableInfo h4,
  .settings__tableInfo a {
    padding-right: 1rem;
    width: auto;
    font-size: 1.6rem;
    font-weight: 500;
    color: var(--color-sub);
  }
  
  .settings__tableInfo p {
    font-size: 1.4rem;
  }
  
  .settings__tableActions {
    text-align: right;
  }
  
  /*=======================
    Login/SignUp
  ========================*/
  
  .auth {
    width: 100%;
    position: relative;
    z-index: 1;
    background: transparent;
    height: 99vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .auth::after {
    z-index: -1;
    opacity: 0.1;
    content: '';
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }
  
  .auth > .card {
    padding: 3rem 4rem;
    width: 95%;
    max-width: 65rem;
  }
  
  @media screen and (min-width: 650px) {
    .auth > .card {
      padding: 5rem 8rem;
      width: 95%;
      max-width: 65rem;
    }
  }
  
  .auth__header h3 {
    color: var(--color-sub);
    font-weight: 700;
    margin-top: 1rem;
  }
  
  .auth__header p {
    font-weight: 400;
    font-size: 1.8rem;
    margin-bottom: 2rem;
  }
  
  .auth__form {
    margin: 3rem 0;
  }
  
  .auth .auth__form label,
  .auth .auth__alternative p {
    font-size: 1.4rem;
    font-weight: 500;
    color: var(--color-sub-light);
  }
  
  .auth .auth__actions {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2.5rem 0;
  }
  .auth .auth__actions a {
    text-decoration: underline;
  }
  
  .auth .auth__alternative a {
    font-size: 2rem;
    font-weight: 500;
  }
  
  /*=======================
    Inbox
  ========================*/
  
  .inbox {
    height: 90vh;
  }
  
  .inbox__title,
  .inbox__title span {
    font-size: 2.4rem;
    font-weight: 700;
    color: var(--color-sub);
    text-transform: uppercase;
    text-align: left;
    margin-bottom: 3rem;
  }
  
  .inbox__title span {
    color: var(--color-main);
  }
  
  .messages {
    list-style: none;
    background: var(--color-white);
    padding: 0.5rem 3rem;
    border: 2px solid var(--color-light);
    border-radius: 0.7rem;
  }
  
  .message {
    padding: 2.5rem 0;
  }
  
  .message > a span {
    font-size: 1.45rem;
    font-weight: 500;
  }
  
  .message.message--unread a span {
    font-weight: 700;
    color: var(--color-sub);
  }
  
  .message:not(:last-child) {
    border-bottom: 2px solid var(--color-white-light);
  }
  
  .message > a {
    display: flex;
    gap: 1rem;
  }
  
  .message__author,
  .message__date {
    flex-basis: 25%;
  }
  
  .message__subject {
    flex-basis: 50%;
  }
  
  .message__date {
    text-align: right;
  }
  
  /*=======================
    Single Message
  ========================*/
  
  .messagePage .message {
    list-style: none;
    background: var(--color-white);
    padding: 3.5rem 5rem;
    border: 2px solid var(--color-light);
    border-radius: 0.7rem;
  }
  
  .messagePage .message__subject {
    font-size: 2.8rem;
    color: var(--color-sub);
    margin-bottom: 1rem;
    margin-top: 2rem;
  }
  
  .messagePage .message__author {
    font-size: 1.8rem;
    font-weight: 500;
  }
  
  .messagePage .message__date {
    font-size: 1.4rem;
    font-weight: 400;
    text-align: left;
    margin-bottom: 3rem;
  }
  
  .backButton {
    background: var(--color-main-light);
    color: var(--color-main);
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
  }
  
  .backButton i {
    font-size: 1.8rem;
  }
  
  
  /*=======================
    Form Page
  ========================*/
  
  .formPage .formWrapper {
    list-style: none;
    background: var(--color-white);
    padding: 3.5rem 5rem;
    border: 2px solid var(--color-light);
    border-radius: 0.7rem;
  }
  
  .formPage .formWrapper__subject {
    font-size: 2.8rem;
    color: var(--color-sub);
    margin-bottom: 1rem;
    margin-top: 2rem;
  }

  .footer {
    background-color: var(--color-sub);
    color:var(--color-main-light);
    padding: 15px;
    text-align: center;
    font-size: 16px;
   
  }
  
  .flex {
    display: flex;
    justify-content: space-between;
  }